@import url(https://cdn.jsdelivr.net/npm/bootstrap-icons@1.4.1/font/bootstrap-icons.css);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,600;0,700;0,800;1,300;1,400;1,600;1,700;1,800&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
:root {
    --primary: #1e88e5;
    --offset: #f1f1f1;
    --offset-dark: #F0F0F0;
    --text: #34373f;

    --header-font: 'Roboto', sans-serif;
    --text-font: 'Open Sans', sans-serif;
}

html, body {
    color: #34373f;
    color: var(--text);
    -webkit-font-smoothing: antialiased;
}

h1, h2, h3, h4, h5, h6 {
    font-family: 'Roboto', sans-serif;
    font-family: var(--header-font);
    font-weight: 300;
    margin: 0;
}

ul, ol {
    list-style: none;
    padding: 0;
}

ul > li {
    padding: 0.2rem 0;
}

ol > li {
    display: inline-block;
    cursor: pointer;
}

a {
    color: inherit;
}

hr {
    margin: 0.9rem 0;
    border-color: #F0F0F0;
    border-color: var(--offset-dark);
    border-top: 0;
    border-left: 0;
}

p {
    margin: 0;
}

.bi {
    color: #1e88e5;
    color: var(--primary);
    font-size: 1.2rem;
    margin-right: 0.125rem;
}

.App {
    background-color: var(--offset);
    font-family: var(--text-font);

    min-height: 100vh;
}

.App > .AppGrid {
    min-height: 100vh;
    
    display: grid;
    grid-template-columns: 1rem 1fr 1rem;
    grid-template-rows: 1rem auto auto auto auto;
    grid-template-areas: 
        ". . ."
        ". MainPanel ."
        ". SkillPanel ."
        ". EducationPanel ."
        "Footer Footer Footer";
    grid-gap: 1rem;
    gap: 1rem;
}
@media (min-width: 1065px) {
    .App > .AppGrid {
        grid-template-columns: 1rem 1fr 1fr 1fr 1rem;
        grid-template-rows: 1rem auto 1fr auto;
        grid-template-areas: 
            ". . . . ."
            ". MainPanel SkillPanel SkillPanel ."
            ". MainPanel EducationPanel EducationPanel ."
            "Footer Footer Footer Footer Footer";
    }
}

@media (min-width: 1600px) {
    .App > .AppGrid {
        grid-template-columns: 12% 25rem 1fr 1fr 12%;
        grid-template-areas: 
            ". . . . ."
            ". MainPanel SkillPanel SkillPanel ."
            ". MainPanel EducationPanel EducationPanel ."
            "Footer Footer Footer Footer Footer";
    }
}

.AppGrid > .AppPanel {
    background-color: #FFF;
    box-shadow: 0 2px 5px 0 rgb(0 0 0 / 16%), 0 2px 10px 0 rgb(0 0 0 / 12%);
}
.MainPanel {
    grid-area: MainPanel;
}

/*
    Escape the parent's padding by reversing it.
    Additionally at bottom some space for the name is created.
*/
.MainPanel .ProfileImage {
    margin-bottom: -2.5rem;
    width: 100%;
    height: auto;
}

.MainPanel .ProfileName {
    font-family: var(--header-font);
    font-size: 2rem;
    color: #FFF;

    position: relative;
    top: -0.5rem;
}

.MainPanel > .MainPanel__Social, 
.MainPanel > .MainPanel__Languages, 
.MainPanel > .MainPanel__Profile,
.MainPanel > .MainPanel__SoftSkills,
.MainPanel > .MainPanel__About {
    padding: 0 1rem 0 1rem;
}

/*
    Social Icons should be in grayscale by default
*/
.MainPanel > .MainPanel__Social .bi-github {
    color: black;
}
.Gaugage {
    width: 100%;
    background-color: var(--offset);
    border-radius: 6px;
    padding: 0.25rem 0.25rem;
    box-sizing: border-box;
    display: -webkit-flex;
    display: flex;
    margin: 0.25rem 0;
}

.Gaugage > .Gaugage__Fill {
    background-color: var(--primary);
    border-radius: 6px;
    height: 1.25rem;
}

.Gaugage > .Gaugage__Text {
    bottom: 0.15rem;
    -webkit-align-self: center;
            align-self: center;
}
.Tabs__Nav {
    margin: 0 0 -1rem 0;
}

.Tabs__NavItem {
    padding: 0 1rem;
    height: 3.5rem;
    line-height: 2.25;
    border-right: 1px solid transparent;

    -webkit-user-select: none;

        -ms-user-select: none;

            user-select: none;

    transition-duration: .2s;
}

.Tabs__NavItem:not(:last-child) {
    border-right: 1px solid var(--offset-dark);
}

.Tabs__NavItem:not(.Tabs__NavItem__Active):hover {
    background-color: var(--offset-dark);
}

.Tabs__NavItem__Active {
    background-color: #FFF;
}

.Tabs__NavItem__Active:last-child {
    border-right: 1px solid var(--offset-dark);
}
.EducationPanel {
    grid-area: EducationPanel;
}

.EducationPanel > .EducationPanel__Nav {
    margin: 0 -1rem -1rem 0;
}

.EducationPanel > .EducationPanel__Nav li {
    clear: both;
    cursor: pointer;
    transition-duration: .15s;
    border: 1px solid transparent;

    padding: 0.8rem;
}

.EducationPanel > .EducationPanel__Nav li:hover:not(.EducationPanel__Nav__Active) {
    background-color: var(--offset-dark);
    border-bottom: 1px solid var(--offset-dark);
}

.EducationPanel > .EducationPanel__Nav li.EducationPanel__Nav__Active {
    background-color: white;
    border-top: 1px solid var(--offset-dark);
    border-left: 1px solid var(--offset-dark);
    border-right: 1px solid var(--offset-dark);
}

.EducationPanel > .EducationPanel__Content {
    padding: 1rem;
}

.EducationPanel .EducationPanel__TabWork__Item_Date {
    margin: 0.75rem 0;
}
.Grid {
    display: grid;
    grid-template-columns: 1fr;
    grid-gap: 1rem;
    gap: 1rem;
}

.Grid:not(:last-child) {
    margin-bottom: 1rem;
}

@media (min-width: 1065px) {
    .Grid {
        grid-template-columns: 1fr 1fr 1fr;
    }
}

.Grid__Item {
    padding: 0.5rem;
    box-shadow: 0 2px 5px 0 rgb(0 0 0 / 8%), 0 2px 10px 0 rgb(0 0 0 / 6%);
}

.Grid__ItemMain {
    display: -webkit-flex;
    display: flex;
}

.Grid__Item > .Grid__ItemMain > img {
    display: inline-block;
    width: 3.5rem;
    height: 3.5rem;
    border-radius: 6px;
}

.Grid__Item > .Grid__ItemMain > div {
    display: inline-block;
    width: 100%;

    padding: 0 0.125rem 0 0.5rem;
}

.Grid__ItemPerks {
    display: grid;
    grid-template-columns: 1fr 1fr;
    font-size: 0.8rem;
    text-align: center;
}

.Grid__ItemPerks > span {
    margin: 0 0.25rem 0.25rem 0.25rem;
}
.SkillPanel {
    grid-area: SkillPanel;
}
.Footer {
    background-color: var(--primary);
    width: 100%;
    min-height: 4rem;

    grid-area: Footer;
}

.Footer > .Footer__Content {
    padding: 1rem;
    color: #FFF;
    text-align: center;
}

.Footer > .Footer__Content p {
    margin: 0.25rem;
}
